<!--
 * @Description:用户列表
 * @LastEditors: Ji Yinghao
 * @Date: 2019-04-30 12:52:55
 * @LastEditTime: 2019-10-09 18:17:25
 -->
<template>
  <div>
    <!-- 导航 -->
    <Breadcrumbs :items="items"/>
    <Module1 ref="module1"/>
    <Module2 ref="module2"/>
    <!-- 数据表格 -->
    <v-card>
      <v-card-title>
        <v-layout row wrap>
          <v-flex v-if="level_type !== 2" xs6 sm4 md2>
            <v-select
              @change="info"
              menu-props="auto"
              v-model="agent_id"
              :items="agent_list"
              label="所属代理商"
              required
            ></v-select>
          </v-flex>
          <v-flex xs6 sm4 md2>
            <v-text-field v-model="user_name" label="用户姓名"></v-text-field>
          </v-flex>
          <v-flex xs6 sm4 md2>
            <v-text-field v-model="user_phone" label="手机号码"></v-text-field>
          </v-flex>
          <v-btn :loading="loader" :disabled="loader" color="primary" @click="info(false)">筛选用户</v-btn>
        </v-layout>
      </v-card-title>
      <!--
        headers : 表头
        items： 表格内容
        total-items ： 总共的数据数量 （手动设置项目的总数，就可以禁用内置排序和分页）
        disable-initial-sort 在初始渲染时禁用默认排序
        :hide-actions="true" 隐藏表格操作
        :dark="true" 暗主题
        :loading="true" 加载进度条
      -->
      <v-data-table
        :headers="headers"
        :items="desserts"
        class="elevation-1"
        :total-items="allList"
        :rows-per-page-text="`每页行数`"
        :rows-per-page-items="pagingList"
        :disable-initial-sort="true"
        :loading="loader"
        @update:pagination="info"
      >
        <template v-slot:items="props">
          <tr @dblclick="info_data( props.item )">
          <td>
            <v-avatar size="36px">
              <img v-if="props.item.thumb_url" :src="props.item.thumb_url" alt="Avatar">
              <v-icon v-else>tag_faces</v-icon>
            </v-avatar>
          </td>
          <td class="text-xs-centre">{{ props.item.phone | cover }}</td>
          <td class="text-xs-centre">{{ props.item.user_name }}</td>
          <td class="text-xs-centre">
            <v-chip small v-if="props.item.agent_name.length == 0"></v-chip>
            <v-chip v-else small v-for="(item1,index) in props.item.agent_name" :key="index">{{ item1 }}</v-chip>
          </td>
          <td class="text-xs-centre">
            <span v-if="props.item.birthday == 0" > ### </span>
            <span v-else >{{ props.item.birthday | birthday }} ( {{ props.item.birthday | age }} )</span>
          </td>
          <!-- <td class="text-xs-centre">{{ props.item.country==""?"###":props.item.country }}</td>
          <td class="text-xs-centre">{{ props.item.city==""?"###":props.item.city }}</td> -->
          <td class="text-xs-centre">{{ props.item.created_time | time }}</td>
          <td class="text-xs-centre">{{ props.item.update_time | time }}</td>
          <td class="text-xs-centre">{{ props.item.count_order }} 笔</td>
          <td class="text-xs-centre">{{ (props.item.charge_amt).toFixed(2) }} 元</td>
          <td class="text-xs-centre">
            <span class="green--text darken-4" v-if="props.item.device_count>0"> 已绑定设备 ({{ props.item.device_count }}) </span>
            <span v-if="props.item.device_count==0" >未绑定设备</span>
          </td>
          <td class="text-xs-centre">
            <v-menu
              bottom
              dark
              origin="center center"
              :close-delay="100"
              transition="slide-x-transition"
            >
              <template v-slot:activator="{ on }">
                <v-btn color="primary" dark v-on="on">操作选项</v-btn>
              </template>
              <v-list>
                <v-list-tile  @click="info_data( props.item )">
                  <v-list-tile-title> <v-icon> assignment_ind </v-icon> 用户详情</v-list-tile-title>
                </v-list-tile>
                <template v-for="(item,index) in root_path">
                  <v-list-tile
                    :key="index"
                    v-if="item.url == '/api/v1/order/list'"
                    @click="to_order( props.item.phone )"
                  >
                    <v-list-tile-title> <v-icon>assignment</v-icon> 用户订单</v-list-tile-title>
                  </v-list-tile>
                </template>
                <v-list-tile  @click="user_equipment( props.item.id )" v-if="props.item.device_count > 0" >
                  <v-list-tile-title> <v-icon>leak_add</v-icon> 关联设备</v-list-tile-title>
                </v-list-tile>
                <!-- <template v-for="(item,index) in root_path">
                  <v-list-tile
                    :key="index"
                    v-if="item.url == '/api/v1/manages/messages'"
                    @click="chatList( props.item )"
                  >
                    <v-list-tile-title>聊天记录</v-list-tile-title>
                  </v-list-tile>
                </template> -->
                <template v-for="(item,index) in root_path">
                  <v-list-tile
                    :key="index"
                    v-if="item.url == '/api/v1/manages/cases_file'"
                    @click="files_data( props.item.id, props.item.thumb_url  )"
                  >
                    <v-list-tile-title> <v-icon>description</v-icon> 病例档案</v-list-tile-title>
                  </v-list-tile>
                </template>
                <template v-for="(item,index) in root_path">
                  <v-list-tile
                    :key="index"
                    v-if="item.url == '/api/v1/user/edit'"
                    @click="modifyMessage( props.item )"
                  >
                    <v-list-tile-title> <v-icon>create</v-icon> 用户修改</v-list-tile-title>
                  </v-list-tile>
                </template>
                <template v-for="(item,index) in root_path">
                  <v-list-tile
                    :key="index"
                    v-if="item.url == '/api/v1/manages/ed_record'"
                    @click="ED_data( props.item.id )"
                  >
                    <v-list-tile-title> <v-icon>format_align_justify</v-icon> ED记录</v-list-tile-title>
                  </v-list-tile>
                </template>
                <template v-for="(item,index) in root_path">
                  <v-list-tile
                    :key="index"
                    v-if="item.url == '/api/v1/manages/ehs'"
                    @click="EHS_data( props.item.id )"
                  >
                    <v-list-tile-title> <v-icon>format_align_left</v-icon> EHS记录</v-list-tile-title>
                  </v-list-tile>
                </template>
                <template v-for="(item,index) in root_path">
                  <v-list-tile
                    :key="index"
                    v-if="item.url == '/api/v1/manages/iief5'"
                    @click="iief5_data( props.item.id )"
                  >
                    <v-list-tile-title> <v-icon>format_align_right</v-icon> IIEF5记录</v-list-tile-title>
                  </v-list-tile>
                </template>
                <template v-for="(item,index) in root_path">
                  <v-list-tile
                    :key="index"
                    v-if="item.url == '/api/v1/user/delete'"
                    @click="del_box( props.item.id )"
                  >
                    <v-list-tile-title class="red--text"> <v-icon style="color: #f44336 !important;">person_add_disabled</v-icon> 删除用户</v-list-tile-title>
                  </v-list-tile>
                </template>
              </v-list>
            </v-menu>
          </td>
          </tr>
        </template>
        <!-- 当没有数据的时候，可以使用 no-data 插槽显示一段自定义HTML。 -->
        <template v-slot:no-data>
          <!-- <v-alert :value="true" color="warning" icon="priority_high" outline>没有更多数据了 :(</v-alert> -->
          <p class="p_text">没有更多数据了 :(</p>
        </template>
        <!-- 如果你还想添加一些额外的功能，比如每列过滤或者搜索，你可以使用 footer 插槽。 -->
        <!-- <template v-slot:footer>
          <td :colspan="headers.length">
            <strong>总计：</strong>
          </td>
        </template>-->
        <!-- 你可以使用page-text 插槽来自定义显示范围以及总条目。 -->
        <template
          v-slot:pageText="props"
        >当前 {{ props.pageStart }} - {{ props.pageStop }} 总共 {{ props.itemsLength }}</template>
      </v-data-table>
    </v-card>
    <!-- 确认框 -->
    <v-dialog v-model="dialog_logout" max-width="290">
      <v-card>
        <v-card-title class="headline">删除操作</v-card-title>
        <v-card-text>确认删除该用户吗？确认删除将不可恢复</v-card-text>
        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn color="green darken-1" flat="flat" @click="dialog_logout = false">取消</v-btn>
          <v-btn color="green darken-1" flat="flat" @click="delMessage">确认删除</v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
  </div>
</template>
<script>
import Breadcrumbs from "../../../components/breadcrumbs/breadcrumbs.vue";
import Module1 from "./components/module1.vue";
import Module2 from "./components/module2.vue";
export default {
  components: {
    Breadcrumbs,
    Module1,
    Module2
  },
  data() {
    return {
      user_name: "",
      user_phone: "",
      agent_name: "",

      agent_id: 0,
      agent_list: [],
      agent_list_data: [],

      // 确认框
      dialog_logout: false,
      user_id: "",
      pagingList: [10, 20, 30, 40, 50, { text: "100", value: 100 }],
      loader: false,
      page: 0,
      page_count: 10,
      allList: 0,
      items: [
        {
          text: "首页",
          disabled: false,
          to: "/home"
        },
        {
          text: "用户列表",
          disabled: false,
          to: "/userList"
        }
      ],
      headers: [
        {
          text: "头像",
          align: "left",
          sortable: false, // 是否可排序
          value: "name"
        },
        { text: "用户手机号", value: "calories", sortable: false },
        { text: "用户姓名", value: "fat", sortable: false },
        { text: "所属代理", value: "fat", sortable: false },
        { text: "生日", value: "carbs", sortable: false },
        // { text: "国家", value: "protein", sortable: false },
        // { text: "城市", value: "iron", sortable: false },
        { text: "用户创建时间", value: "iron", sortable: false },
        { text: "上次更新时间", value: "iron", sortable: false },
        { text: "订单笔数", value: "iron", sortable: false },
        { text: "充值金额", value: "iron", sortable: false },
        { text: "状态", value: "iron", sortable: false },
        { text: "操作", value: "iron", sortable: false }
      ],
      desserts: [],
      root_path: this.$cookie.getLocalStorage("root_path").root_path,
      level_type: this.$cookie.getLocalStorage("root_path").level
    };
  },
  mounted: function() {
    this.agent_data();
  },
  methods: {
    // 用户订单
    to_order( phone ){
      this.$router.push({
        name: 'order',
			  params: {
			    "phone": phone
			  }
      });
    },
    // 用户设备
    user_equipment( user_id ){
      this.$router.push({
        path: `/deviceList`,
        query: {
          user_id: user_id
        }
      });
    },
    // 代理列表
    agent_data() {
      this.$axios({
        method: "post",
        url: this.$apis.URL.manages_query_agent,
        data: {
          id: -1,
          level: 2
        }
      })
        .then(response => {
          this.agent_list.push("全部代理");
          this.agent_list_data = response.data.datas;
          if (response.data.result === 0) {
            this.agent_list_data.forEach(item => {
              this.agent_list.push(item.user_name);
            });
          } else {
            this.$Dialog.getProductCheckResult_({text: response.data.message,timeout: 3000, snackbar: true });
          }
          // 代理列表跳转带参
          if (this.$route.query.agent_name !== undefined) {
            this.agent_id = this.$route.query.agent_name;
            this.info( false );
          }
        })
        .catch(error => {
          this.$Dialog.getProductCheckResult_({text: "😡" + error,timeout: 3000, snackbar: true });
        });
    },
    // ED记录
    ED_data(id) {
      this.$router.push({
        path: `/ED`,
        query: {
          user_id: id
        }
      });
    },
    // EHS记录
    EHS_data(id) {
      this.$router.push({
        path: `/EHS`,
        query: {
          user_id: id
        }
      });
    },
    // iief5记录
    iief5_data(id) {
      this.$router.push({
        path: `/iief5`,
        query: {
          user_id: id
        }
      });
    },
    // 病例档案
    files_data(id,img) {
      this.$router.push({
        path: `/files`,
        query: {
          user_id: id,
          img: img
        }
      });
    },
    // 用户详情
    info_data(list) {
      let datas = {
        state: true,
        list: list
      };
      this.$refs.module2.info(datas);
      
    },
    // 修改操作
    modifyMessage(list) {
      let datas = {
        state: true,
        list: list
      };
      this.$refs.module1.info(datas);
    },
    // 聊天记录
    // chatList(data) {
    //   this.$router.push({
    //     path: `/chatList`,
    //     query: {
    //       user_data: data
    //     }
    //   });
    // },
    // 确认删除
    del_box(id) {
      this.user_id = id;
      this.dialog_logout = true;
    },
    // 删除用户操作
    delMessage() {
      this.$axios({
        method: "post",
        url: this.$apis.URL.user_delete,
        data: {
          user_id: this.user_id
        }
      })
        .then(response => {
          if (response.data.result === 0) {
            this.$Dialog.getProductCheckResult_({text: "用户已删除，操作完成",timeout: 3000, snackbar: true });
            this.dialog_logout = false;
            this.info( false );
          } else {
            this.$Dialog.getProductCheckResult_({text: response.data.message,timeout: 3000, snackbar: true });
          }
        })
        .catch(error => {
          this.$Dialog.getProductCheckResult_({text: error,timeout: 3000, snackbar: true });
        });
    },
    loader_() {
      this.loader = true;
      setTimeout(() => (this.loader = false), 500);
    },
    // 用户列表
    info(event) {
      this.loader = true;
      let agent_id_ = 0;
      this.agent_list_data.forEach(item => {
        if (this.agent_id == item.user_name) {
          agent_id_ = item.id;
          return false;
        }
      });
      this.$axios({
        method: "post",
        url: this.$apis.URL.user_list,
        data: {
          agent_id: agent_id_,
          user_name: this.user_name,
          phone: this.user_phone,
          "page": event==false?this.page:event.page-1,
          "page_count": event==false?this.page_count:event.rowsPerPage
        }
      })
        .then(response => {
          if (response.data.result === 0) {
            this.desserts = response.data.datas.list;
            this.allList = response.data.datas.total_number;
          } else {
            this.$Dialog.getProductCheckResult_({text: response.data.message,timeout: 3000, snackbar: true });
          }
          this.loader = false;
        })
        .catch(error => {
          this.$Dialog.getProductCheckResult_({text: error,timeout: 3000, snackbar: true });
        });
    }
  },
  watch: {}
};
</script>

<style lang="less" scoped>
.flex {
  padding-right: 10px;
  padding-left: 10px;
}
</style>
