<!--
 * @Description: 角色详情信息弹出框模块
 * @LastEditors: Ji Yinghao
 * @Date: 2019-05-05 13:42:39
 * @LastEditTime: 2019-10-07 16:07:59
 -->
<template>
  <div id="switch__">
    <!-- <v-btn color="primary" @click="dialog = true" >+ 创建子账号123 </v-btn> -->
    <v-layout row justify-center>
      <v-dialog v-model="dialog" max-width="800px">
        <!-- <template v-slot:activator="{ on }">
          <v-btn color="primary" flat dark v-on="on">+ 创建 {{ dialog }} </v-btn>
        </template>-->

        <v-card>
          <v-card-title>
            <span class="headline">用户基本信息</span>
          </v-card-title>
          <v-card-text>
            <p class="root_p">
              <v-img
                v-if="data.thumb_url != ''"
                :src="data.thumb_url"
                lazy-src="https://picsum.photos/id/11/10/6"
                aspect-ratio="1"
                class="grey lighten-2"
                max-width="60"
                @click="img_(data.thumb_url)"
              ></v-img>
              <v-img
                v-else
                src="https://picsum.photos/id/11/10/6"
                lazy-src="https://picsum.photos/id/11/10/6"
                aspect-ratio="1"
                class="grey lighten-2"
                max-width="60"
              ></v-img>
            </p>

            <p class="root_p">
              <span class="order_title">手机号码</span>
              <v-chip>{{ data.phone | cover }}</v-chip>
            </p>

            <p class="root_p">
              <span class="order_title">所属位置</span>
              <v-chip>{{ location }}</v-chip>
            </p>

            <p class="root_p">
              <span class="order_title">用户姓名</span>
              <v-chip>{{ data.user_name }}</v-chip>
            </p>

            <p class="root_p" v-if=" data.agent_name.length > 0" >
              <span class="order_title">所属代理</span>
              <v-chip v-for="(item,index) in data.agent_name" :key="index" >{{ item }}</v-chip>
            </p>

            <p class="root_p" v-else >
              <span class="order_title">所属代理</span>
              <v-chip >{{ "暂无记录" }}</v-chip>
            </p>

            <p class="root_p">
              <span class="order_title">用户生日</span>
              <v-chip>{{ data.birthday==0?"暂无记录":data.birthday }}</v-chip>
            </p>

            <p class="root_p">
              <span class="order_title">订单笔数</span>
              <v-chip>{{ data.count_order }}</v-chip>
            </p>

            <p class="root_p">
              <span class="order_title">充值金额</span>
              <v-chip>{{ (data.charge_amt).toFixed(2) }} 元 </v-chip>
            </p>

            <p class="root_p">
              <span class="order_title">用户创建时间</span>
              <v-chip>{{ data.created_time | time }}</v-chip>
            </p>

            <p class="root_p">
              <span class="order_title">上次更新时间</span>
              <v-chip>{{ data.update_time | time }}</v-chip>
            </p>

            <!-- <p class="root_p" >已获得的权限</p>
                  <v-flex md12 sm12>
                   <v-chip class="text-xs-center" v-for="(item,index) in data.root_path" :key="index" >{{ item.name }}</v-chip>
            </v-flex>-->
          </v-card-text>
          <v-card-actions>
            <v-spacer></v-spacer>
            <v-btn color="info darken-1" @click="dialog = false">确认</v-btn>
          </v-card-actions>
        </v-card>
      </v-dialog>
    </v-layout>

    <!-- 右上角提示框 -->
    <v-snackbar
      v-model="alert.snackbar"
      :right="true"
      :timeout="alert.timeout"
      :top="true"
      :multi-line="true"
    >
      {{ alert.text }}
      <v-btn color="pink" flat @click="alert.snackbar = false">确认</v-btn>
    </v-snackbar>
  </div>
</template>

<script>
export default {
  data() {
    return {
      alert: {
        text: "",
        timeout: 3000,
        snackbar: false
      },
      dialog: false,
      data: {
        agent_name:[],
        charge_amt: 0,
        thumb_url:""
      },
      // 所属地
      location:'正在获取...'
    };
  },
  // 更新完成
  mounted: function() {},
  methods: {
    /** 初始化数据 */
    info(data) {
      this.dialog = data.state;
      this.data = data.list;
      this.data.thumb_url == ''? this.data.thumb_url = ``:this.data.thumb_url;
      this.$axios({
        method: "post",
        url: this.$apis.URL.user_location,
        data: {
          phone: this.data.phone
        }
      })
        .then(response => {
          if (response.data.result === 0) {
            if(response.data.datas.response[this.data.phone] !== null){
              this.location = response.data.datas.response[this.data.phone].location;
            }else{
              this.location = '获取位置失败'
            }
          }else{
            this.$Dialog.getProductCheckResult_({text: response.data.message, timeout: 3000, snackbar: true });
          }
        })
        .catch(error => {
          this.$Dialog.getProductCheckResult_({text: "😡" + error,timeout: 3000, snackbar: true });
        });
    },
    /** 图片预览 */
    img_( img ){
      if( img == '' ){
        return
      }
      window.open(img,'newwindow', `height=600, width=800, top=${ document.body.clientHeight/2 - 200 }, left=${ document.body.clientWidth/2 - 400 }, toolbar=no, menubar=no, scrollbars=no, resizable=no,location=no, status=no`);
    }
  }
};
</script>

<style lang="less">
#switch__ {
  .v-input__slot {
    margin-bottom: 0px !important;
  }
  .v-input--selection-controls {
    margin-top: 0px !important;
  }
  .v-list__tile__content {
    display: block !important;
  }
  .v-list__tile--avatar {
    min-height: 70px;
    display: inline-table;
  }
}
.root_p {
  font-size: 16px;
  padding-left: 17px;
}
.order_title{
  display: inline-block;
  width: 125px;
}
</style>
